import { compact } from "lodash"
import { PrivateSharedContentOwner } from "../file.types"

type GetOwnersForAPIProps = {
  ownersSelected: Array<{ value: string; label: string }> | undefined
  ownersList: Array<PrivateSharedContentOwner> | undefined
}

function getOwnersForAPI({ ownersSelected, ownersList }: GetOwnersForAPIProps): Array<string> {
  if (!ownersList?.length || !ownersSelected?.length || ownersSelected.length === ownersList.length)
    return []

  return compact(
    ownersSelected.map(ownerSelected => {
      const ownerRecord = ownersList.find(owner => owner.userId === ownerSelected.value)
      if (!ownerRecord) return null

      return ownerRecord.legacyId || ownerRecord.userId
    }),
  )
}

export { getOwnersForAPI }
